.mainHeading {
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.mainHeading * {
    margin-top: 0px;
    margin-bottom: 0px;
}

 
.gredingCon p {
    align-items: flex-start;
    font-size: small;
    color: gray;
}

.mainBodyCon {
    background-color: #f6f6f6;
    width: 56%;
    padding: 25px 30px 25px 25px;
}

.searchCon {
    display: flex;
    align-items: center;
    background-color: white;
    padding: 7px;
    border-radius: 10px;
    margin-right: 10px;

}

.searchCon img {
    height: 20px;
    width: 20px;
    margin-right: 10px;
}

.inputEle {
    border: none;
    background-color: transparent;
}

.inputEle:focus {
    border: none;
    outline: none;
}

.notificationCon {
    background-color: white;
    padding: 5px 7px;
    border-radius: 10px;
}

.notificationCon img {
    height: 20px;
    width: 20px;
}

.headCorner {
    display: flex;
}

/* heading */
.banner {
    margin-top: 4vh;
    width: 100%;
    height: 25vh;
    background-image: url(../Images/banner_img.png);
    border-radius: 10px;

}

.blur {
    backdrop-filter: blur(10px);

    width: 93%;
    height: 10vh;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;

    padding: 20px;
    color: white;

}

.blur h2 {
    font-size: larger;
    display: flex;
}

.blur * {
    margin-top: 0px;
    margin-bottom: 0px;
}

.wearth {
    float: right;
    height: 40px;
    width: 40px;
}

.blur p {
    margin-top: 10px;
    font-size: small;
}

.banner button {
    margin-top: 10px;
    margin-left: 20px;
    background-color: white;
    border: none;
    border-radius: 10px;
    padding: 7px;
}

.yourCourse {
    margin: 30px 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.yourCourse p {
    color: gray;
    font-size: small;
}

.coursecardCon {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
}

.courseCard {
    width: 90%;
    background-color: white;
    height: 45vh;
    padding: 7px;
    border-radius: 10px;



}

.imgCon {
    width: 100%;
    height: 45%;
    object-fit: contain;

    background-image: url(../Images/card_photo.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    border-radius: 10px;
}

.cardImg {
    width: 100%;
    border-radius: 10px;
}

.level {
    display: inline-block;
    width: auto;
    background-color: white;
    position: absolute;
    bottom: 85px;
    margin-left: 10px;
    border-radius: 10px;
    padding: 5px;
    display: flex;
    align-items: center;

}

.level img {
    margin-left: 7px;
    height: 15px;
    width: 15px;
}

.propic {
    width: 30px;
    float: right;
    margin-top: 15px;
    border-radius: 50%;
}

.role {
    display: inline-block;
    margin-top: 5px;
    font-size: x-small;
    color: gray;

}

.cardGridCon {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: space-evenly;
    align-items: center;
    gap: 20px;

}

.smallcard {
    width: 70%;
    height: 20px;
    padding: 5px;
    background-color: #fafafa;
    display: flex;
    align-items: center;
    border-radius: 40%;
}

.smallcard img {
    margin-right: 5px;
    width: 15px;
}

.smallcard p {
    font-size: x-small;
}

.progresCon {
    margin-top: 10px;
    width: 100%;
    background-color: #f3f3f3;
    border-radius: 25px;
    overflow: hidden;
}

.percentage {
    height: 5px;
    background-color: #040404;
    width: 0;
    border-radius: 25px;
    transition: width 0.5s;
}

.progresstextCon {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: x-small;

}

.progresstextCon * {
    margin-top: 7px;
    margin-bottom: 0px;
}

.instCom {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 50px 50px;
    gap: 10px;
}

.instcard {
    width: 85%;
    padding: 10px;
    background-color: white;
    display: flex;
    align-items: center;
    border-radius: 10px;
    justify-content: space-between;
}
.instcard img{
    width: 40px;
    height: 40px;
    border-radius: 10px;
}
.instinfo{
    width: 50%;
    display: flex;
    align-items: flex-start;
    justify-items: flex-start;
    flex-direction: column;
}
.instinfo *{
    margin-top: 0px;
    margin-bottom: 0px;
}
.instinfo h4{
    font-size: small;
}
.instinfo p{
    font-size: x-small;
    color: gray;
}
.instcard button{
    background-color: transparent;
    border: 1px solid black;
    height: 30px;
    border-radius: 10px;
    margin-right: 10px;
}
.instcard button:hover{
background-color: #323338;
color: white;
}