.ProfileInfoCon {
    width: 30%;
    padding: 20px;
    height: 200vh;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}

.profileImgCon {
    width: 100%;
    height: 200px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 8px 24px;
    border-radius: 10px;
}

.probgimg {
    background-image: url(../Images/card_photo.png);
    width: 100%;
    height: 45%;
    object-fit: contain;

    background-image: url(../Images/profile_BG.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    border-radius: 10px;
    display: flex;
    align-items: end;
    justify-content: center;
}

.whiteCon {
    background-color: white;
    width: 100%;
    height: 50%;
    z-index: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;


}

.whiteCon * {
    margin-top: 0px;
    margin-bottom: 0px;
}

.clsimg {
    margin-top: -10px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    z-index: 1;
}

.btick {
    width: 15px;
}

.progridCon {

    padding: 10px;
    margin-top: 10px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 5px;
}

.progrid {
    border: 1px solid black;
    width: 40%;
    height: 40px;
    padding: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    border-radius: 10px;
}

.progrid img {
    width: 20px;
}

.progrid p {
    margin: 0px;
    font-size: small;
    margin-top: 7px;
}

.calCon {
    margin-top: 30PX;
    border-radius: 10px;
    width: 95%;
    height: 120px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 8px 24px;
    padding: 10px;
}

.calhead {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.calhead * {
    margin-top: 0px;
    margin-bottom: 0px;

}

.calGridCon {
    display: grid;
    margin-top: 20px;
    grid-template-columns: repeat(7, 1fr);
    gap: 20px;
}

.calcardCon {
    padding: 5px;
    width: 50%;
    height: 50px;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    font-size: x-small;
    align-items: center;
    justify-content: space-between;
    border-radius: 50px;
}
.calcardCon:hover{
    background-color: #37383c;
    color: white;
}
/* Chat GPT */
.calcardCon:hover > .data:not(:hover) {
   color: #37383c;
  }
