.date:hover{

}

.date {
    margin-top: 10px;
    background-color: white;
    padding: 5px;
    border-radius: 50%;
    height: 10px;
    width: 10px;
    text-align: center;
}

.calcardCon * {
    margin-top: 0px;
    margin-bottom: 0px;
}

.taskCon{
margin-top: 10px;
height: 220px;
box-shadow: rgba(0, 0, 0, 0.2) 0px 8px 24px;
border-radius: 10px;
display: flex;
flex-direction: column;
align-items: center;
}
    
.taskHead{
    margin-top: 15px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 10px;
    width: 90%;

}
.taskHead *{
margin-top: 0px;
margin-left: 0px;
}
.taskHead p{
    font-size: xx-small;
}
.taskList{
    width: 90%;
    border: 0.5px solid gray;
    height: 40px;
    padding: 10px;
    display: flex;
    align-content: center;
    justify-content: space-between;
    border-radius: 10px;
   margin-top: 10px;
}
.taskList img{
    width: 30px;
    height: 30px;
    background-color: rgb(255, 108, 108);
    padding: 5px;
    border-radius: 10px;
   

}
.titCon{
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.titCon *{
    margin-top: 0px;
    margin-bottom: 0px;
}
.titCon p{
    font-size: x-small;
    color: gray;
    margin-top: 5px;
}






